<template>
    <div>
        <Head headback="false" headto="true" bol="搜索"></Head>
        <div class="content">
            <div class="shopsearch">
                <input type="text" placeholder="请输入商家或美食名称" v-model="txt" @focus="getg()">
                <i class="iconfont iconchahao" v-if="bool" @click="getqu()"></i>
                <button @click="getgain()">提交</button>
            </div>
            <div class="list" v-if="searchbol">
                <ul class="list1" v-if="bal">
                    <li v-if="bol" class="seashop">商家</li>
                    <li class='searchlist' v-for="(item,index) of merchant" :key="index" v-if="bol">
                      <img :src='`//elm.cangdu.org/img/${item.image_path}`' alt="">
                      <div>
                        <p class="p1">
                          {{item.name}}
                          <svg data-v-3726cde6="" xmlns="http://www.w3.org/2000/svg" version="1.1" width="24" height="14" class="pay_icon">
                            <polygon data-v-3726cde6="" points="0,14 4,0 24,0 20,14" style="fill: none; stroke: rgb(255, 96, 0); stroke-width: 1;"></polygon>
                            <line data-v-3726cde6="" x1="1.5" y1="12" x2="20" y2="12" style="stroke: rgb(255, 96, 0); stroke-width: 1.5;"></line> 
                            <text data-v-3726cde6="" x="3.5" y="9" style="fill: rgb(255, 96, 0); font-size: 9px; font-weight: bold;">支付</text>
                          </svg>  
                        </p>
                        <p class="p2">月售{{item.recent_order_num}}单</p>
                        <p class="p2">{{item.float_minimum_order_amount}}元起送 / 距离{{item.distance}}</p>
                      </div>
                    </li>
                    <li v-if="!bol" class="sorry">很抱歉！无搜索结果</li>
                </ul>
                <div class="list2" v-if="!bal">
                    <p>搜索历史</p>
                    <ul>
                        <li v-for="(item,index) of searchhistory" :key="index">
                            <span  @click='gettxt(item)'>{{item}}</span>
                            <i class="iconfont iconchahao" @click="getdate2(index)"></i>
                        </li>
                        <li class="date1" @click="getdate1()"><span>清空搜索记录</span></li>
                    </ul>
                </div>
            </div>
        </div>
        <Foot></Foot>
    </div>
</template>

<script>
import Head from "./../header/Head";
import Foot from "./../footer/Foot";
export default {
  components: {
    Head,
    Foot
  },
  data() {
    return {
      txt: "",
      bool: false,
      geohash: 0,
      merchant:[],
      bol:true,
      bal:true,
      searchhistory:[],
      searchbol:false
    };
  },
  methods: {
    //点击X号清除输入框内容
    getqu() {
      this.txt = "";
      this.bal=false
    },
    getg() {
      if (this.txt != "") {
        this.bool = true;
      } else {
        this.bool = false;
      }
    },
    //提交按钮，点击通过接口获取搜索关键字返回的内容,并搜索关键字添加到搜索记录里，存到本地存储
    getgain() {
        this.bal=true
        this.searchbol=true
      if (this.txt != "") {
           
        var that = this;
        this.http.get("http://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash="+this.geohash+"&keyword="+this.txt+"&type=search").then(res => {
            console.log(res)
            if(res.length!=0||res.message!="搜索餐馆数据失败"){
                this.merchant=res
                this.bol=true
                this.bal=true
            }
            if(res.length==0||res.message=="搜索餐馆数据失败"){
                this.bol=false
            }
          });
         if (localStorage.getItem("gethistory")) {
        
        this.searchhistory = JSON.parse(localStorage.getItem("gethistory"));
      
      }
     if (this.searchhistory.length == 0) {
          this.searchhistory.push(this.txt);
          localStorage.setItem("gethistory", JSON.stringify(this.searchhistory));
        }else{
           var r =this.searchhistory.filter(item => {
          return item==this.txt
        });
        if (r.length == 0) {
          this.searchhistory.push(this.txt);
          localStorage.setItem("gethistory", JSON.stringify(this.searchhistory));
        }
        }
      }
    },
    //清空搜索记录
    getdate1(){
         this.searchhistory=[];
          localStorage.setItem("gethistory", JSON.stringify(this.searchhistory));
          this.searchbol=false
    },
    //删除对应的一项
    getdate2(index){
        this.searchhistory.splice(index,1)
        localStorage.setItem("gethistory", JSON.stringify(this.searchhistory));
        if (this.searchhistory.length==0){
        this.searchbol=false
    }
    },
    //点击搜索记录进行搜索
    gettxt(item){
      this.txt=item
      this.getgain()
    }
  },
  mounted() {
    if (localStorage.getItem("geta")) {
      var arr = JSON.parse(localStorage.getItem("geta"));
      for (let i in arr) {
        this.geohash = arr[i].geohash;
        console.log(this.geohash);
      }
    }
     if (localStorage.getItem("gethistory")) {
        
        this.searchhistory = JSON.parse(localStorage.getItem("gethistory"));
         if (this.searchhistory.length==0){
        this.searchbol=false
    }else {
        this.searchbol=true
        this.bal=false
    }
      }
    
  },
  watch: {
    //监听input框的输入，如果有内容就让X号显示，没有就让X号消失
    txt: function() {
      if (this.txt != "") {
        this.bool = true;
      } else {
        this.bool = false;
        this.bal=false
      }
    }
  }
};
</script>

<style  lang="scss" scoped>
.content {
  width: 100%;
  height: 100%;
  padding-top: 0.88rem;
  padding-bottom: 1.2rem;
  .shopsearch {
    width: 100%;
    height: 1.18rem;
    background: white;
    display: flex;
    position: relative;
    input {
      width: 5.3rem;
      height: 0.68rem;
      border: 0.01rem solid #e4e4e4;
      background: #f5f5f5;
      border-radius: 0.06rem;
      outline: none;
      padding-left: 0.1rem;
      margin-left: 0.23rem;
      margin-top: 0.23rem;
      font-size: 0.34rem;
      font-weight: bold;
    }
    i {
      font-size: 0.24rem;
      color: blue;
      font-weight: bold;
      position: absolute;
      left: 5.3rem;
      top: 0.48rem;
    }
    button {
      width: 1.54rem;
      height: 0.7rem;
      background: #3190e8;
      color: white;
      font-weight: bold;
      border: 0.01rem solid #3190e8;
      border-radius: 0.06rem;
      font-size: 0.34rem;
      margin-left: 0.1rem;
      margin-top: 0.23rem;
      outline: none;
    }
  }
  .list {
      width: 100%;
      height: auto;
      .list1 {
          width: 100%;
          height: 100%;
          margin-top: 0.06rem;
          li {
              width: 100%;
              height: 0.82rem;
              background: white;
          }
          .searchlist {
            width: 100%;
            height: 1.98rem;
            border-bottom: 0.01rem solid #ccc;
            display: flex;
            img {
              width: 0.8rem;
              height: 0.8rem;
              margin-left: 0.23rem;
              margin-top: 0.23rem;
            }
            div {
              width: 6.12rem;
              height: 1.5rem;
              margin-top: 0.23rem;
              margin-left: 0.12rem;
              border-bottom: 0.01rem solid #ccc;
              .p1 {
                font-size: 0.3rem;
                text-align: left;
                width: 100%;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
              .p2 {
                font-size: 0.3rem;
                text-align: left;
                width: 100%;
                margin-top: 0.1rem;
              }
            }
          }
          .seashop{
              width: 7.3rem;
              height: 0.94rem;
              text-align: left;
              padding-left: 0.2rem;
              line-height: 0.94rem;
              font-size: 0.4rem;
              font-weight: bold;
              color: gray;
              background: #f5f5f5;
          }
          .sorry {
              font-size: 0.4rem;
              line-height: 0.82rem;
          }
      }
      .list2 {
          width: 100%;
          height: auto;
          p {
              width: 7.3rem;
              height: 0.94rem;
              text-align: left;
              padding-left: 0.2rem;
              line-height: 0.94rem;
              font-size: 0.4rem;
              font-weight: bold;
              color: gray;
          }
          ul {
              width: 100%;
              height: 100%;
              li {
                  font-size: 0.3rem;
                  background: white;
                  line-height: 0.82rem;
                  display: flex;
                  justify-content: space-between;
                  border-top:0.01rem solid #ccc; 
                  .iconchahao {
                      margin-right: 0.3rem;
                  }
                  span {
                      width: 5rem;
                      height: 0.82rem;
                      text-align: left;
                      margin-left: 0.2rem;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                  }
              }
              .date1 {
                  border-top: 0.01rem solid  #ccc;
                  span {
                      margin: 0 auto;
                      text-align: center;
                  }
              }
          }
      }
  }
}
</style>



